import React, { useState } from 'react';
import { Button, Text, Flex, Link, Box } from 'rebass';
import {
  BOOK_LINK,
  BLOG_LINK,
  PORTING_BLOG_LINK,
  CHARGER_LINK,
  MAX_WIDTH,
} from '../common';
import * as firebase from 'firebase/app';
import 'firebase/firestore';

interface IProps {
  onStartOver: () => void;
}

export default function Credit({ onStartOver }: IProps) {
  const [email, setEmail] = useState('');
  const [showEmailInput, setShowEmailInput] = useState(true);

  function sendEmail() {
    if (email.length === 0 || !email.includes('@')) {
      alert('이메일을 입력해주세요.');
      return;
    }
    firebase.initializeApp({
      apiKey: 'AIzaSyAqFjFNaHIkFnc4ihEMckBtoSziTUGOVjc',
      authDomain: 'react-quiz-e5ba2.firebaseapp.com',
      databaseURL: 'https://react-quiz-e5ba2.firebaseio.com',
      projectId: 'react-quiz-e5ba2',
      storageBucket: 'react-quiz-e5ba2.appspot.com',
      messagingSenderId: '323295321801',
      appId: '1:323295321801:web:f2368c6aa4c9ea5f',
    });
    firebase
      .firestore()
      .collection('email-list')
      .add({ email });
    setShowEmailInput(false);
    alert('참여해주셔서 감사합니다 😁');
  }

  return (
    <Flex
      flexDirection="column"
      alignItems="center"
      px="20px"
      style={{
        width: '100%',
        height: '100vh',
        color: 'white',
        fontWeight: 'bold',
        maxWidth: MAX_WIDTH,
        margin: '40px auto',
      }}
    >
      <Text>축하합니다🎉🎉🎉 모든 문제를 맞혔습니다</Text>
      <Text fontSize="30px">💯</Text>
      <Box mt="20px" />
      <Text>
        안녕하세요. 저는{' '}
        <Link
          href={PORTING_BLOG_LINK}
          target="_blank"
          style={{ color: 'white' }}
        >
          [카카오페이지 웹 React 포팅 후기]
        </Link>
        를 쓴 이재승입니다.
      </Text>
      <Box mt="20px" />
      <Text lineHeight="20px">
        제가 집필한 [
        <Link href={BOOK_LINK} target="_blank" style={{ color: 'white' }}>
          실전 리액트 프로그래밍
        </Link>
        ] 책의 출간 소식을 알리기 위해 이 사이트를 만들었습니다 😁
      </Text>
      <Box mt="20px" />
      <Text width="100%">책 출간 기념으로 이벤트가 진행중입니다 🎁</Text>
      <Text width="100%">
        이메일 주소를 남겨주시면 랜덤 추첨으로 아래 경품을 드립니다.
      </Text>
      <Text width="100%" fontSize="12px">
        * 2019년 6월 14일까지 참여 가능합니다.
      </Text>
      <Text width="100%" fontSize="12px">
        * 당첨자는 입력해주신 이메일로 알려드리며, 2019년 6월 15일에 제{' '}
        <Link href={BLOG_LINK} target="_blank" style={{ color: 'white' }}>
          블로그
        </Link>
        에서 공지합니다. (이메일 일부 마스킹)
      </Text>
      <Text width="100%">
        - 고속 충전기 2명 (
        <Link href={CHARGER_LINK} target="_blank" style={{ color: 'white' }}>
          제품 상세
        </Link>
        ) 🔌🚀🚀🚀
      </Text>
      <Text width="100%">- [실전 리액트 프로그래밍] 도서 5명 📖</Text>
      <Text width="100%">- 스타벅스 2만원 상품권 10명 ☕️🍰</Text>
      <Box mt="40px" />
      {showEmailInput && (
        <>
          <input
            type="email"
            value={email}
            onChange={e => setEmail(e.currentTarget.value)}
            placeholder="이메일을 입력해주세요."
            style={{ width: '50%' }}
          />
          <Button
            bg="black"
            mt="20px"
            onClick={sendEmail}
            style={{ cursor: 'pointer' }}
          >
            등록하기
          </Button>
        </>
      )}
      <Box mt="40px" />
      <Text>
        Powered by{' '}
        <Link
          href="https://reactjs.org/"
          target="_blank"
          style={{ color: 'white' }}
        >
          react
        </Link>
        ,{' '}
        <Link
          href="https://github.com/mojs/mojs"
          target="_blank"
          style={{ color: 'white' }}
        >
          mojs
        </Link>
      </Text>
      <Button
        bg="black"
        mt="20px"
        onClick={onStartOver}
        style={{ cursor: 'pointer' }}
      >
        처음으로
      </Button>
    </Flex>
  );
}
